import React, {useEffect, memo, useState} from 'react';
import {Link, withRouter} from "react-router-dom";
import {useTranslation} from "react-i18next";
import {SignIn} from "../../../entity/Redux/Auth/SignIn/Action"
import {SignUp} from "../../../entity/Redux/Auth/SignUp/Action"
import {VerifyCode} from "../../../entity/Redux/Auth/SignUp/Action"
import $ from 'jquery';
import M from 'materialize-css';
// import {GetToken} from '../../../services/Authentication/GetToken';
import {TOKEN_KEY, SECRET_KEY} from '../../../services/Authentication/Types'
import {useDispatch, connect} from 'react-redux';
import CryptoJs from 'crypto-js'

let Login = memo((props) => {

    useEffect(() => {
        if (props.LoginRes.data) {
            $("#repass_").css({"left": "-100%"});
            localStorage.setItem("verifyCode", props.LoginRes.data);
            M.toast({html: 'verify code : ' + props.LoginRes.data})
        }
        if (props.LogRes) {
            console.log("success : ", props.LogRes)
            if (props.LogRes.status == 200) {
                if (props.LogRes.statusText = "OK") {

                    localStorage.setItem(TOKEN_KEY, props.LogRes.token);
                    // if (props.LogRes.data.accountType === 2) {
                        window.location.pathname = "/doctor"
                    // }
                } else {
                    alert("password is fail");
                }
            } else {
                console.log("error : ", props.LogRes);
            }
        }
        if (props.Token) {
            // const enc_token = CryptoJs.AES.encrypt(JSON.stringify(props.LoginRes.accessToken), SECRET_KEY).toString();
            // const bytes = CryptoJs.AES.decrypt(enc_token, SECRET_KEY);
            // const decrypted = JSON.parse(bytes.toString(CryptoJs.enc.Utf8));
            // const token = decrypted.token;
            localStorage.setItem(TOKEN_KEY, props.Token);
            window.location.pathname = '/your-details';
        }
        var elems = document.querySelectorAll('.tabs');
        M.Tabs.init(elems);
        var tabh = document.querySelectorAll('select');
        M.FormSelect.init(tabh);
        var body = $('#wrapper');
        function goToNextInput(e) {
            var key = e.which,
                t = $(e.target),
                sib = t.next('input');
            if (key != 9 && (key < 48 || key > 57)) {
                e.preventDefault();
                return false;
            }
            if (key === 9) {
                return true;
            }
            if (!sib || !sib.length) {
                sib = body.find('input').eq(0);
            }
            sib.select().focus();
        }
        function onKeyDown(e) {
            var key = e.which;

            if (key === 9 || (key >= 48 && key <= 57)) {
                return true;
            }

            e.preventDefault();
            return false;
        }

        function onFocus(e) {
            $(e.target).select();
        }


        body.on('keyup', 'input', goToNextInput);
        body.on('keydown', 'input', onKeyDown);
        body.on('click', 'input', onFocus);


    })
    const dispatch = useDispatch();
    const {t, i18n} = useTranslation();

    function setNewPass() {
        $("#loading_back").fadeIn()
        setTimeout(function () {
            $("#loading_back").fadeOut();
            // props.history.push('/login/verify/userdata');
            window.location.pathname = '/your-data'
        }, 3000);
    }

    const [codeVerify, setCodeVerify] = useState(null);

    function verifyCodeGet(e) {
        setCodeVerify({
            ...codeVerify,
            [e.target.name]: e.target.value
        })
    }


    const [loginData, setLoginData] = useState(null);
    const [registerData, setRegisterData] = useState({phoneNumber: null, userType: 1});

    function inputLoginChange(e) {
        setLoginData({
            ...loginData,
            [e.target.name]: e.target.value
        })
    }


    function inputRegisterChange(e) {
        var phoneNumber = $("input[name=phoneNumber]").val();
        var userType = $("input[name=doctor]:checked").val();
        localStorage.setItem("rols", userType);
        setRegisterData({
            ...registerData,
            phoneNumber: phoneNumber,
            userType: userType
        })
    }

    function verify_code() {
        $("#loading_back").fadeIn()
        setCodeVerify({
            ...codeVerify,
            phoneNumber: registerData.phoneNumber
        })
        var phoneNumber = registerData.phoneNumber;
        if (phoneNumber == "" || phoneNumber == null) {
            alert("phone number must filled");
        } else {
            setTimeout(function () {
                $("#loading_back").fadeOut();
                dispatch(SignUp(registerData))
            }, 3000);
        }
    }

    const [verifyJsonData, setVerifyJsonData] = useState({})

    function handleVerifyCode() {
        setVerifyJsonData({
            ...verifyJsonData,
            codeVerify: 1
        })
        if (verifyJsonData) {
            dispatch(VerifyCode(codeVerify));
        }
    }


    function redirectToDash(e) {
        e.preventDefault();
        dispatch(SignIn(loginData))


        $("#loading_back").fadeIn()
        setTimeout(function () {
            $("#loading_back").fadeOut();
            // if (username == "admin-top") {
            //     window.location.pathname = '/admin-top'
            // } else if (username == "admin") {
            //     window.location.pathname = '/admin'
            // } else if (username == "doctor") {
            //     window.location.pathname = '/doctor'
            // } else {
            // }
        }, 3000);
    }

    function handleLang(e) {
        // e.preventDefault();
        if (!localStorage.getItem("lan")) {
            localStorage.setItem("lan", 'en');
        }
        var lan = localStorage.getItem("lan");
        var ckeckbox = $("input[name=cahngelan]");
        if (ckeckbox.is(":checked") && lan === 'en') {
            localStorage.setItem("lan", 'ch');
        } else {
            localStorage.setItem("lan", 'en');
        }
        let lang = localStorage.getItem('lan');
        i18n.changeLanguage(lang);
    }


    function handleCheckFormVerify(e) {
        e.preventDefault();


        // console.log("verify data : " , codeVerify);

        dispatch(VerifyCode(codeVerify))

    }

    const select_type = t('select-type.l');
    return (
        <div className="auth_back">
            <div id="back_shado"></div>

            <div style={{width: '31rem'}} id="auth_box">
                <div id="change_or_sub">
                    <input type="checkbox" name="cahngelan" onChange={handleLang} id="change_lang_login"/>
                    <span>{t('Lang.l')}</span>
                    {/*<span></span>*/}
                </div>

                <div className={`mini_box_over`}>
                    <div id={`mini_circule`}>
                        <i className="material-icons">person_outline</i>
                    </div>
                </div>
                <div className={`mini_box_over`} style={{right: 0}}>
                    <div id={`mini_circule_r`}>
                        <img src="/img/icons/doctorB.png"/>
                    </div>
                </div>
                <div className="container">
                    <div className="row">


                        <div className="row">
                            <div className="input-field col s12" style={{marginTop: 55}}>
                                <p id="text_sub">{t('Welcome-to.l')}</p>
                                <p id="text_sub" style={{
                                    fontSize: "40px",
                                    marginTop: "15px !important",
                                    marginBottom: "15px !important"
                                }}>Bi Con</p>
                                <p id="text_sub">{t('Backend-panel.l')}</p>
                            </div>
                        </div>

                        <div className="register_type_user" id="verifyBorder">

                            <div id="loading_back">


                                <div className="preloader-wrapper big active" id="loading_progress">
                                    <div className="spinner-layer spinner-blue-only">
                                        <div className="circle-clipper left">
                                            <div className="circle"></div>
                                        </div>
                                        <div className="gap-patch">
                                            <div className="circle"></div>
                                        </div>
                                        <div className="circle-clipper right">
                                            <div className="circle"></div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className="doctor_form">
                                <ul id="tabs-swipe-demo" className="tabs">
                                    <li className="tab col s6"><a className="active"
                                                                  href="#test-swipe-1">{t('Login.l')}</a></li>
                                    <li className="tab col s6"><a
                                        href="#test-swipe-2">{t('Register.l')}</a></li>
                                </ul>
                                <div id="test-swipe-1">
                                    <form id="login"
                                          onSubmit={e => redirectToDash(e)}
                                          className="col s12" style={{
                                        paddingLeft: 15,
                                        paddingRight: 15,
                                        paddingTop: 5,
                                        paddingBottom: 10
                                    }}>
                                        <div className="row" style={{marginBottom: 'none !important'}}>
                                            <div className="input-field col s12" style={{marginTop: 0}}>
                                                <p style={{color: '#ccc'}}>&nbsp;&nbsp;{t('Phone-num.l')}</p>
                                                <div id="input_back">
                                                    <input onChange={inputLoginChange} lang="zh"
                                                           placeholder={t('U-phone-num.l')} autoComplete="false"
                                                           name="loginPhonenumber"
                                                           required type="text"
                                                           className="validate"/>
                                                </div>
                                                <p id="alert-p">&nbsp;&nbsp;{t('must-field.l')}</p>

                                                {/*<label htmlFor="password">Password</label>*/}
                                            </div>
                                        </div>
                                        <div className="row" style={{marginBottom: 'none !important'}}>
                                            <div className="input-field col s12" style={{marginTop: 0}}>
                                                <p style={{color: '#ccc'}}>&nbsp;&nbsp;{t('Password.l')}</p>
                                                <div id="input_back">

                                                    <input placeholder={t('UPassword.l')} autoComplete="false"
                                                           name="loginPassword"
                                                           onChange={inputLoginChange}
                                                           required type="password"
                                                           className="validate"/>
                                                </div>
                                                <p id="alert-p">&nbsp;&nbsp;{t('must-field.l')}</p>
                                                {/*<label htmlFor="password">Password</label>*/}
                                            </div>
                                        </div>

                                        <div className={`col s12`}>
                                            <div className={`row`}>
                                                <div className={`col s6`} style={{marginTop: 20, marginBottom: 20}}>
                                                    <button type="submit"
                                                            className="btn blue darken-4 waves-effect">{t('SignIn.l')}</button>
                                                </div>

                                            </div>
                                            <div className="col s12" style={{padding: 0}}>
                                                <span style={{float: "left", color: "#fff"}}>{t("Forget.l")}</span><Link
                                                to="/repass"><span id="forget_pass">{t("Reset.l")}</span></Link>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                                <div id="test-swipe-2" style={{height: '220px'}}>
                                    <div id="repass_">
                                        <div className="container">
                                            <div className="row">
                                                <div className="col s6">
                                                    <div className="input-field col s12" style={{marginTop: 0}}>
                                                        <p style={{color: '#ccc'}}>&nbsp;&nbsp;{t('Phone-num.l')}</p>
                                                        <div id="input_back">
                                                            <input placeholder={t('U-phone-num.l')} autoComplete="false"
                                                                   name="phoneNumber"
                                                                   min="0" max="9"
                                                                   pattern="[0-9]{1}"
                                                                   onChange={e => inputRegisterChange(e)}
                                                                   required type="text" className="validate"/>
                                                        </div>
                                                        <p id="alert-p">&nbsp;&nbsp;{t('must-field.l')}</p>
                                                        {/*<label htmlFor="password">Password</label>*/}
                                                    </div>
                                                    <div className={`col s6`}>
                                                        {/*<Link to="/login/verify">*/}
                                                        <a className="btn blue darken-4 waves-effect"
                                                           onClick={verify_code}
                                                           style={{textTransform: 'unset !important'}}
                                                           id="check_phone_num">{t('SignUp.l')}</a>
                                                        {/*</Link>*/}
                                                    </div>
                                                    <div className={`col s6`}>

                                                        <p>
                                                            <input name="doctor" onChange={e => inputRegisterChange(e)}
                                                                   id="doctor-radio" className="with-gap"
                                                                   value="2" type="radio"
                                                                   checked/>
                                                            <label htmlFor="doctor-radio">Doctor</label>
                                                            &nbsp;&nbsp;&nbsp;
                                                            <input name="doctor" className="with-gap"
                                                                   onChange={e => inputRegisterChange(e)} value="1"
                                                                   id="admin-radio" type="radio"/>
                                                            <label htmlFor="admin-radio">Admin</label>
                                                        </p>
                                                    </div>

                                                </div>

                                                <form onSubmit={e => handleCheckFormVerify(e)}>

                                                    <div className="col s6">
                                                        <div className="input-field col s12" style={{marginTop: 0}}>
                                                            <p style={{color: '#ccc'}}>&nbsp;&nbsp;{t('Verify.l')}</p>

                                                            <div id="wrapper">
                                                                <div id="dialog">

                                                                    <div id="form">

                                                                        <input type="text" placeholder="--"
                                                                               maxLength="1"
                                                                               size="1"
                                                                               name="code_1"
                                                                               onChange={e => verifyCodeGet(e)}
                                                                               min="0" max="9"
                                                                               pattern="[0-9]{1}"/>
                                                                        <input type="text" placeholder="--"
                                                                               maxLength="1"
                                                                               size="1"
                                                                               onChange={e => verifyCodeGet(e)}
                                                                               name="code_2"
                                                                               min="0" max="9"
                                                                               pattern="[0-9]{1}"/>
                                                                        <input type="text" placeholder="--"
                                                                               maxLength="1"
                                                                               size="1"
                                                                               onChange={e => verifyCodeGet(e)}
                                                                               name="code_3"
                                                                               min="0" max="9"
                                                                               pattern="[0-9]{1}"/>
                                                                        <input type="text" placeholder="--"
                                                                               maxLength="1"
                                                                               size="1"
                                                                               name="code_4"
                                                                               min="0" max="9"
                                                                               onChange={e => verifyCodeGet(e)}
                                                                               pattern="[0-9]{1}"/>


                                                                    </div>

                                                                </div>

                                                            </div>


                                                            <p id="alert-p">&nbsp;&nbsp;{t('must-field.l')}</p>

                                                            {/*<label htmlFor="password">Password</label>*/}
                                                        </div>

                                                        <div className={`col s6`}>
                                                            {/*<NavLink to={{pathname : '/login/verify' }}>*/}
                                                            <button type="submit"
                                                                    style={{textTransform: 'unset !important'}}
                                                                    id="check_code"
                                                                    className="btn blue darken-4 waves-effect">{t('check.l')}</button>
                                                            {/*</NavLink>*/}
                                                        </div>
                                                    </div>


                                                </form>

                                            </div>
                                        </div>

                                    </div>


                                </div>
                            </div>

                        </div>
                    </div>
                </div>

            </div>

        </div>
    );
})

const mapStateToProps = (state) => {
    return {LoginRes: state.Auth.res, Token: state.Auth.token, LogRes: state.Auth.resLog}
}

export default connect(mapStateToProps, {SignIn, SignUp, VerifyCode})(Login);
