import ErrorHandler from '../../../../PageComponent/MicroComponent/ErroHandler'
import {SECRET_KEY, TOKEN_KEY} from '../../../../services/Authentication/Types'
import {RequestApi as request} from '../api'
import history from '../../../Model/History/BrowserHistory';
// import {Redirect} from "react-router-dom";

export const SignIn = (data) => {

    const jsonData = JSON.stringify({
        phoneNumber: data.loginPhonenumber,
        password: data.loginPassword
    });

    return async (dispatch, getState) => {
        await request.SignIn(jsonData)
            .then(response => {
                dispatch({
                    type: 'SIGN_IN_SUCCESS',
                    response: response
                })
            } , error => {
                dispatch({
                    type : 'SIGN_IN_ERROR',
                    resError : "error"
                })
            })
    }
}