import { request as Request  , request_global as GlobalReq} from "../../../services/Connection/Request";
import { Path as Api } from "./Types";

export const RequestApi = {
    SignIn: (data) => Request.post("auth/login" , data ),
    SignUp: (data) => Request.post("auth/register" , data),
    VerifyCode : (data) => Request.post("auth/verifyActiveCode" , data),
    ForgetPassword : (data) => Request.post("auth/forgotPassword/sendActiveCode" , data),
    NewPassword : (data) => Request.post("auth/resetPassword" , data),
    UserFirstData : (data) => Request.put("auth/updateDetails" , data),
    DoctorAdd : (data) => Request.post("doctors" , data),
}